@import "ScssHelpers/helpers-all.scss";

.section_head {
	display: flex;
	justify-content: flex-start;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 3.4rem;
		}
		@include bp($point_4 - 1) {
			margin-bottom: 1.6rem;
		}
	}

	@include bp($point_4, min) {
		align-items: center;
	}
	@include bp($point_4 - 1) {
		align-items: flex-end;
	}
}

.section_head__title {
	@at-root {
		h2#{&} {
			@include h2;
			letter-spacing: -.02em;
		}
		h3#{&} {
			@include h3;
		}
	}

	&:not(:last-child) {
		margin-right: 1.2rem;
	}
}

.section_head__right {
	margin-left: auto;

	@include bp($point_4 - 1) {
		padding-bottom: 4px;
	}
}
