@import "ScssHelpers/helpers-all.scss";

.content_banner {
	&:not(:last-child) {
		margin-bottom: 3.8rem;
	}
}

.content_banner__wrap {
	background-color: $gray_3;

	@include bp($point_4, min) {
		@include flex_block(row, nowrap, flex-start, stretch);
	}

	@include bp($point_4 - 1) {
		@include flex_block(column-reverse, nowrap, flex-start, stretch);
	}
}

.content_banner__content {
	@include bp($point_4, min) {
		padding: 3.8rem 4.8rem;
	}

	@include bp($point_4 - 1) {
		padding: 2.4rem 2.4rem 2.8rem;
	}
}

.content_banner__title {
	@include h4;

	line-height: 1.14;

	@include bp($point_4, min) {
		letter-spacing: -0.015em;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

	@include bp($point_4 - 1) {
		letter-spacing: -0.026em;

		&:not(:last-child) {
			margin-bottom: 0.8rem;
		}
	}
}

.content_banner__text {
	@include text;

	@include bp($point_4, min) {
		line-height: 1.5;
		letter-spacing: -0.015em;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	@include bp($point_4 - 1) {
		line-height: 1.42;
		letter-spacing: -0.016em;

		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}
	}
}

.content_banner__picture {
	flex-shrink: 0;
	position: relative;

	// height: 0;

	@include bp($point_4, min) {
		width: 29.17%;
		padding-top: 19.6%;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		padding-top: 55.8%;
	}
}

.content_banner_img {
	@include stretch;
}

.content_banner_img__in {
	@include cover_img;
}

.content_banner__btn {
	@include bp($point_2, min) {
	}
	@include bp($point_3, $point_4) {
	}
	@include bp($point_4 - 1) {
		width: 100%;
	}
}
