@import "ScssHelpers/helpers-all.scss";

.products_section {

	@include bp($point_2, min) {
		.content_banner_video + & {
			padding-top: 1.5rem;
		}
	}

	&.products_section--no_desktop_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}
}

.product_card {
	position: relative;
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	justify-content: flex-start;
	align-items: flex-start;
	color: $black;

	@include bp($point_4 - 1) {
		.products_list_venison & {
			display: flex;
			align-items: flex-start;
			justify-content: flex-start;
			flex-direction: row;
		}
	}
}

.product_card__content {
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	justify-content: flex-start;
	align-items: flex-start;

	height: 100%;
}

.product_card__full_link {
	color: $black;

	display: flex;
	flex-direction: column;
	flex: 1 1 auto;
	width: 100%;

	&:hover,
	&:focus {
		text-decoration: none;

		.product_card__title {
			color: $gold_2;
		}

		.product_card__img {
			scale: 1.08;
		}
	}
}

.product_card__image {
	position: relative;
	display: block;
	width: 100%;
	height: 0;

	overflow: hidden;

	&:hover,
	&:focus {
		.product_card__img {
			scale: 1.06;
		}
	}

	.products_section & {
		@include bp($point_2, min) {
			padding-bottom: 106.6%;
		}
		@include bp($point_3) {
			padding-bottom: 100.6%;
		}
	}

	.recently_viewed & {
		@include bp($point_2, min) {
			padding-bottom: 111.6%;
		}
		@include bp($point_3) {
			padding-bottom: 104.6%;
		}
	}

	@include bp($point_4, min) {
		padding-bottom: 100.8%;

		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}

	@include bp($point_4 - 1) {
		padding-bottom: 102.4%;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	.products_list_venison & {
		@include bp($point_4 - 1) {
			width: 37.6%;
			flex-shrink: 0;
			padding-bottom: 36.4%;
			margin-bottom: 0;

			&:not(:last-child) {
				margin-right: 2.2rem;
			}
		}
	}
}

.product_card__img {
	@include stretch;

	transition: scale 0.3s ease;
}

.product_card__img_in {
	@include cover_img;
}

.product_card__title {
	color: $black;

	font-weight: 500;
	font-style: normal;

	transition: color 0.3s ease;

	@include bp($point_2, min) {
		font-size: 2.8rem;
		letter-spacing: -0.016em;
		line-height: 1.16;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	@include bp($point_3) {
		font-size: 1.8rem;
		letter-spacing: -0.022em;
		line-height: 1.12;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
}

.product_card__title a {
	color: $black;
	transition: color 0.3s linear;

	&:hover,
	&:focus {
		text-decoration: none;
		color: $gold_2;
	}
}

.product_card__excerpt {
	margin-top: auto;

	color: $gray_1;
	font-weight: 500;
	text-transform: uppercase;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1rem;
		}
		@include bp($point_3) {
			margin-bottom: 0.2rem;
		}
	}

	@include bp($point_2, min) {
		font-size: 1.6rem;
		letter-spacing: 0.07em;
		line-height: 1.13;
	}

	@include bp($point_3) {
		font-size: 1.2rem;
		letter-spacing: 0.12em;
		line-height: 1.33;
	}

	@include bp($point_4 - 1) {
		.products_list_venison & {
			margin-top: 0;
		}
	}
}

.product_card__rating {
	display: flex;
	align-items: center;

	color: $gold_1;

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}

	@include bp($point_3) {
		img {
			width: 6.8rem;
		}
	}
}

.prod_spotlight__rating {
	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.product_card__price {
	display: flex;
	align-items: center;

	color: $black;
	font-weight: 500;
	letter-spacing: 0.042em;
	line-height: normal;
	text-align: left;
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-size: 2.2rem;
	}
	@include bp($point_3) {
		font-size: 1.6rem;
	}

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.product_card__old_price {
	margin-right: 1rem;

	font-weight: 300;
	color: $gray_1;

	text-decoration: line-through;
}

.product_card__badge {
	position: absolute;
	left: 0;

	font-weight: 500;
	text-transform: uppercase;
	color: $white;
	background-color: $black;
	z-index: 1;

	@include bp($point_2, min) {
		top: 2.4rem;

		padding: 0.8rem 1.4rem;

		font-size: 1.5rem;
	}
	@include bp($point_3) {
		top: 1.4rem;
		padding: 0.6rem 1rem;

		font-size: 1rem;
	}

	@include bp($point_6) {
		max-width: 10rem;
	}
}

.product_card__badge--sold_mod {
	letter-spacing: 0.08em;
	background-color: $brown_2;
}

.product_card__badge--exclusive_mod {
	letter-spacing: 0.056em;
	background-color: #a6898d;
}

.product_card__link,
.product_card__buttons {
	@include bp($point_5, min) {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
	}
}

.product_card__buttons {
	flex-wrap: wrap;

	@include bp($point_5, min) {
		margin: 0 -1.6rem -1.6rem 0;
	}

	@include bp($point_4 - 1) {
		.products_list & {
			display: none;
		}
	}

	@include bp($point_5 - 1) {
		width: 100%;
	}
}

.product_card__link {
	&:not(:first-child) {
		margin-top: 1.8rem;
	}

	@include bp($point_5 - 1) {
		width: 100%;
	}
}

.product_card__btn:first-child {
	@include bp($point_5, min) {
		padding: 0 1.6rem 1.6rem 0;
	}

	@include bp($point_5 - 1) {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}
}
