@import "ScssHelpers/helpers-all.scss";

.products_list_venison {
	@include bp($point_4, min) {
		margin: 0 -2.4rem 2.6rem 0;

		@include flex_block(row, wrap, flex-start, stretch);
	}
	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 3.6rem;
		}
	}
}
.products_list_venison__item {
	@include bp($point_2, min) {
		width: 25%;
	}

	@include bp($point_4, min) {
		padding: 0 2.4rem 5rem 0;
	}

	@include bp($point_3, $point_4) {
		width: 33.333%;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}
}
