@import "ScssHelpers/helpers-all.scss";

.recently_viewed {
}

.recently_viewed__wrap {
	position: relative;
}

.recently_viewed__button {
	position: absolute;
	top: 0;
	z-index: 3;

	&.recently_viewed__button--prev_mod {
		left: 0;
		margin-left: -2rem;
	}
	&.recently_viewed__button--next_mod {
		right: 0;
		margin-right: -2rem;
	}

	@include bp($point_4, min) {
		margin-top: 14%;
	}
	@include bp($point_4 - 1) {
		margin-top: 19%;
	}
}

.recently_viewed__slider {
	&.swiper {
		@include bp($point_2, min) {
			margin-right: -2.4rem;
		}
		@include bp($point_3) {
			margin-right: -1.5rem;
		}
	}
}

.recently_viewed__list {
	@include flex_block(row, nowrap, flex-start, stretch);
}

.recently_viewed__item {
	&.swiper-slide {
		height: auto;
	}

	@include bp($point_2, min) {
		padding-right: 2.4rem;
	}
	@include bp($point_3) {
		padding-right: 1.5rem;
	}

	@include bp($point_2, min) {
		width: 25%;
	}
	@include bp($point_3, $point_4) {
		width: 33.333%;
	}
	@include bp($point_4 - 1) {
		width: 50%;
	}
}
