@import "ScssHelpers/helpers-all.scss";

.woocommerce-pagination {
	text-align: center;
	margin: 0 auto;
}

.page-numbers {
	display: flex;
	align-items: center;
	justify-content: center;

	// @include bp($point_3) {
	// 	display: none;
	// }
}

.page-numbers li {
	margin: 0 1rem;
}

.page-numbers li .page-numbers:not(.slider_btn_2) {
	position: relative;
	font-size: 2.2rem;
	color: $black;
	transition: color 0.3s linear;
}

.page-numbers li a.page-numbers:not(.slider_btn_2) {
	&:hover,
	&:focus {
		color: $gold_2;
		text-decoration: none;
	}
}

.page-numbers li .page-numbers.prev {
	margin-right: 0.7rem;
	transform: rotate(180deg);
}

.page-numbers li .page-numbers.next {
	margin-left: 0.7rem;
}

.page-numbers li .page-numbers.prev,
.page-numbers li .page-numbers.next {
	// color: $gold_2;

	@include bp($point_4, min) {
		padding: 1.2rem 1.8rem;
	}

	@include bp($point_4 - 1) {
		padding: 1rem 1rem;
	}
}

.page-numbers li .page-numbers.current {
	font-weight: 500;

	&:after {
		content: "";
		position: absolute;
		left: 0;
		right: -2px;
		bottom: -2px;
		height: 3px;
		background-color: $black;
	}
}
